<template>
  <div class="box">
    <el-container>
      <el-aside width="50%">
        <div style=" padding-top: 18px;">
          <label>原文</label>
          <button @click="translate">获取新文章</button>
          <el-input
            class="text"
            type="textarea"
            :autosize="{ minRows: 22, maxRows: 22}"
            placeholder="请输入文章内容"
            v-model="originalText"
          ></el-input>
        </div>
      </el-aside>
      <el-main>
        <div>
          <label>伪原文</label>
          <el-input
            class="text"
            type="textarea"
            :autosize="{ minRows: 22, maxRows: 22}"
            v-model="result"
          ></el-input>
        </div>
      </el-main>
    </el-container>
  </div>
</template>

<script>
export default {
  name: "pagePermissions",
  data() {
    return {
      originalText: "",
      result: ""
    };
  },
  methods: {
    translate() {
      const loading = this.$loading({
        lock: true,
        text: "Loading",
        spinner: "el-icon-loading",
        background: "rgba(0, 0, 0, 0.7)"
      });

      this.$axios({
        method: "post",
        // url: "http://localhost:8888/api/login",
        // method: "get",
        url: this.$baseURL + "/systemTools/rewrite",
        data: { query: this.originalText },

        headers: {
          "Content-Type": "application/json;charset=UTF-8",
          token: this.$store.getters.token
        }
      })
        .then(response => {
          console.log(response);
          loading.close();

          if (response.data.ok) {
            this.result = response.data.content;
          } else {
            this.$message({
              showClose: true,
              message: response.data.message,
              type: "error"
            });
          }
        })
        .catch(error => {
          console.log(error); //请求失败返回的数据
        });
    }
  }
};
</script>

<style lang="scss" scoped>
.box {
  height: 100%;
  border: 1px #909399 solid;
  padding: 15px;
  .text{
    margin-top: 14px;
  }
  button {
    background-color: #78d6fd;
    border-radius: 4px;
    border: 0;
    padding: 2px 18px;
    margin-left: 40px;
    color: #fff;
  }
}
</style>



